.Navbar{
    position: relative;
    width: 1530px;
    
    
}
.listNav{
    display: flex;
    flex-direction: row;
    background-color: rgb(240, 240, 240);
  
}
.listNav1>a>img{
    position: relative;
    bottom: 30%;
    left: 7%;
    width: 20%;
}
.listNav>li,.listNav2>li{
    list-style: none;
    
}
.listNav2{
    position: relative;
    right: 10%;
    display: flex;
    flex-direction: row;
}
.listNav2>li{
    margin-right: 5%;
    font-size: 20px;
    font-family:Georgia, 'Times New Roman', Times, serif;

}
.listNav2{
    position: relative;
    top: 30%;
    margin-right: 5%;
}

.listNav{
    height: 80px;
}
.listNav3>#navName1,#navName2,#navName3,#navName4{
    color: black;
}
#navName1:hover,#navName2:hover,#navName3:hover,#navName4:hover{
    color: rgb(134, 74, 0);
}

.listNav12{
    display: none;
}
#Get{
    position: relative;
    bottom: 20px;
    display: inline;
width: 100%;
height: 70px;
background-color: rgb(110, 54, 14);
border: none;

}
#Logout{
    display: none;
}

@media only screen and (max-width: 800px) and (min-width:10px){
    .listNav12{
        display: flex;
        width: 150px;
        position: relative;
        bottom: 50%;
    }  
    .listNav12>img{
        display: flex;
        width: 200px;
    } 
}