

.responsive {
    position: relative;
    right: 20%;
    width: 139%;
    height: -50%;
    background-color: brown;
    margin: 0 auto;
    
  }

  .header{
    color:white;
    position: absolute;
    top: 30%;
    width: 100%;
    text-align: center;
    font-size: 50px;
    right: 20%;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif
  }

  .p {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 30px;
    text-align: center;
    color: black;
    margin-top: 5%;
  }

  


  h2{
    text-align: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color:black;
    float: right;
  }

  .para h2{
    text-align: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin-top: 5%;
  }
  
  .para{
    font-size: 50px;
    margin-top: 5%;
    font: 1em sans-serif;
    margin-left: 0.5%;
    word-spacing: 5px;
    color: black;
    display: grid;
    margin-left: -100%;
 }

  .para h4{
    margin-top: 2%;
    color: white;
    font-size: 20px;
    margin-bottom: 5%;
     background-color: grey;
  }

  
  

  *, *:before, *:after {
    box-sizing: inherit;
  }
  

  .column {
    float: left;
    width: 25%;
    margin-bottom: 16px;
    padding: 0 8px;
   
    
  }
  
  .card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin: 8px;
  }
  .drum {
    padding: 0 16px;
  }
  
  .drum::after, .row::after {
    content: "";
    clear: both;
    display: table;
  }
  
  .title {
    color: grey;
  }
  
  .button {
    border: none;
    outline: 0;
    display: inline-block;
    padding: 8px;
    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
  }
  
  .button:hover {
    background-color: #555;
  }
  
  @media screen and (max-width: 780px) {
    .column {
      width: 100%;
      display: block;
    }
  }
  